.header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 40px;
    padding: 2rem 0;
    border-bottom: 1px solid var(--dark-graish-blue);
    box-shadow: 0px 4px 2px -3px var(--dark-graish-blue);

    &__h1 {
        padding-bottom: 0.6rem;
    }

    &__ul {
        list-style-type: none;
        display: flex;
        gap: 10px;
    }

    &__li {
        display: inline-block;
    }

    &__anchor {
        text-decoration: none;
        padding: 0.7rem;
        font-size: 18px;
        transition: all 150ms;

        &:any-link {
            color: var(--dark-graish-blue);
        }

        &:hover, &:focus {
            outline: 3px solid var(--dark-graish-blue);
            border-radius: 15px;
            text-shadow: 1px 1px 2px var(--primary-orange);
            color: var(--primary-orange);
            font-weight: 500;
        }
    }

    &__cart {
        width: 22px;
        height: 20px;
        cursor: pointer;
        margin-left: auto;
    }

    &__avatar {
        width: 50px;
        height: 50px;
        align-self: center;
    }
}